body {
  margin: 0;
}

.tooltip {
  color: #fff;
  background-color: #7e7e7e;
  border: 2px solid #000;
  border-radius: 2px;
  padding: 10px;
  position: relative;
  transform: translateY(0);
}

.tooltip:after {
  content: "";
  z-index: -1;
  background-color: #7e7e7e;
  border: 2px solid #000;
  border-color: #0000 #000 #000 #0000;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 90%;
  left: 50%;
  transform: rotateZ(45deg)translateX(-50%);
}
