body {
    margin: 0
}

.tooltip {
    /* background-image: linear-gradient(transparent, #7e7e7e); */
    background-color: #7e7e7e;
    color: white;
    padding: 10px;
    position: relative;
    border: 2px solid black;
    border-radius: 2px;
    transform: translateY(0px)
}

.tooltip::after {
    position: absolute;
    content: '';
    width: 20px;
    height: 20px;
    background-color: #7e7e7e;
    /* background-image: linear-gradient(right, transparent, #7e7e7e);
    background-image: linear-gradient(right, #7e7e7e, transparent); */
    top: 90%;
    left: 50%;
    transform: rotateZ(45deg) translateX(-50%);
    z-index: -1;
    border-bottom: 2px solid black;
    border-top: 2px solid transparent;
    border-left: 2px solid transparent;
    border-right: 2px solid black;
}